<template>
  <group class="jq-mt">
  <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
   </group>
  <group class="jq-mt">
    <div class="chart" v-ebarline="barLineData"></div>
  </group>
  <group class="jq-mt">
        <flexbox class=" just" :margin-left=0 style="height:0.5rem;">

      <flexbox-item class=" test testTitle"><div>区分</div></flexbox-item>

      <flexbox-item class=" test testTitle"><div>目标MA</div></flexbox-item>

      <flexbox-item class=" test testTitle"><div>实际MA</div></flexbox-item>


    </flexbox>
  <flexbox class="" :margin-left=0 style="height:0.5rem;" v-for="x in resList">

      <flexbox-item class=" test"><div>总接单</div></flexbox-item>

      <flexbox-item class="test"><div>{{x.RECEIVING_}}</div></flexbox-item>

      <flexbox-item class=" test"><div>{{x.TARGET_}}</div></flexbox-item>

    </flexbox>
  </group>
</template>

<script>
  //import {Group,Flexbox,FlexboxItem,PopupPicker} from 'vux/src/components'
  import {Flexbox,FlexboxItem} from '*/flexbox'
  import PopupPicker from '*/popup-picker'
  import Group from '*/group'
  import { getList } from '../../../untils/api'
  export default {
    components: {
    Group,
     PopupPicker,
    FlexboxItem,
    Flexbox
  },
  data () {
    return {
      barLineData:{},
      dateTile:'日期',
      time: formatData.defaultTime,
      timeList: formatData.dateList,
       permissions:localStorage.getItem("permissions"),
      resList:[]
    }
  },
  ready(){
     document.title='接单';
  },
  methods: {
    async getData() {
        commit('UPDATE_LOADING', true)
      let id = "";
      if(this.permissions=='201'){
        id='APP10121';
      }else if(this.permissions=='202'){
          id='APP10123';
      }else if(this.permissions=='203'){
         id='APP10122';
      }

      let date=formatData.dateConversion(this.time);
      let dataTit = await getList(id,{date_:date});
      if(dataTit.success){

        const TableList = dataTit.data;
        this.barLineData = {};
        this.barLineData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
          commit('UPDATE_LOADING', false)
      }else{
          commit('UPDATE_LOADING', false)
        console.log(dataTit.message);
      }

    },
    async getBarLineData() {
        commit('UPDATE_LOADING', true)
    let id = "";
     if(this.permissions=='201'){
        id='APP10406';
      }else if(this.permissions=='202'){
          id='APP10213';
      }else if(this.permissions=='203'){
         id='APP10306';
      }

    let date=formatData.dateConversion(this.time);
    let dataTit = await getList(id,{date_:date});
      if(dataTit.success){

        //表头：
        //表格:
          this.resList = dataTit.data.result.list
             commit('UPDATE_LOADING', false)
      }else{
          commit('UPDATE_LOADING', false)
        console.log(dataTit.message);
      }
    }
  },
   watch: {
     time (newtime, oldtime) {
      this.getData();
      this.getBarLineData();
       formatData.setDateTime(newtime);
    }
  },
  created: function () {
    this.barLineData = {};
    this.barLineData.dataset = [];
    this.getData();
    this.getBarLineData();
  }
  }

</script>






